let routes = [
  {
    path: '/view-proposal',
    component: ViewProposalPage,
  },
  {
    path: '/create-proposal',
    component: CreateProposalPage,
  },
  {
    path: '/create-benefit',
    component: CreateBenefit,
  },
  {
    path: "/dashboard",
    component: Dashboard,
  },
  {
    path: "/",
    component: HomePage,
  },
];
const prefix = '/underwriting';
routes.map(route => (route.path = `${prefix}${route.path}`));


export default function App() {
  return (
    <div>
      <ThemeProvider theme={theme}>
        <StylesProvider injectFirst>
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <Helmet titleTemplate="NEO - %s" defaultTitle="NEO">
              <meta name="description" content="NEO application" />
            </Helmet>
            <CssBaseline>
              <Switch>
                {renderRoutes(routes)}
                <Route path="" component={NotFoundPage} />
              </Switch>
            </CssBaseline>
          </MuiPickersUtilsProvider>
        </StylesProvider>
      </ThemeProvider>
    </div>
  );
}